document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
});

function iniciarApp(){

    navegacionFija();
    crearGaleria();
    scrollNav();
}

// Mantiene la barra fija cuando se llega a line up
function navegacionFija(){
    const barra = document.querySelector('.header');
    const sobreFestival = document.querySelector('.sobre-festival'); // Clase desde _festival.scss
    const body = document.querySelector('body');


    window.addEventListener('scroll', function(){
        if( sobreFestival.getBoundingClientRect().bottom < 0){
            barra.classList.add('fijo'); // clase desde _header.scss
            body.classList.add('body-scroll'); // Clase desde _globales.scss
        } else {
            barra.classList.remove('fijo'); // clase desde _header.scss
            body.classList.remove('body-scroll'); // Clase desde _globales.scss
        }
    });
}

// Nos lleva al contenido del enlace de manera dinamica
function scrollNav(){
    const enlaces = document.querySelectorAll('.navegacion-principal a');
    
    enlaces.forEach( enlace => {
        enlace.addEventListener('click', function(e){

            e.preventDefault();// Quitamos el valor default que manda de golpe al contenido

            const seccionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(seccionScroll);
            seccion.scrollIntoView({ behavior: "smooth"}); // Aplicamos el efecto Smooth

        });
    });
}


//Crear galeria en miniatura
function crearGaleria(){
    const galeria = document.querySelector('.galeria-imagenes');

   for(let i = 1; i <=12; i++){
        const imagen = document.createElement('picture');
        // Establecemos la estructura del HTML
        imagen.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif">
            <source srcset="build/img/thumb/${i}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="imagen galeria">
        `;

        //Callbacks
        imagen.onclick = function(){
            mostrarImagen(i);
        }

        galeria.appendChild(imagen);//Insertan las imagenes a DOM
   }

}



function mostrarImagen(id){ // Callbacks

    const imagen = document.createElement('picture');
        // Establecemos la estructura del HTML
        imagen.innerHTML = `
        <source srcset="build/img/grande/${id}.avif" type="image/avif">
        <source srcset="build/img/grande/${id}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="imagen galeria">
    `;

    // Crear el Overlay con la imagen
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen); //Insertamos la imagen en DIV
    overlay.classList.add('overlay'); // Agregamos la clase

    // Cierra el Modal tocando en cualquier lado de la pantalla.
    overlay.onclick = function(){
        const body = document.querySelector('body');
        body.classList.remove('fijar-body'); // Clase desde _globales.scss
        overlay.remove();
    }


    // Boton para cerrar el Modal
    const cerrarModal = document.createElement('P');
    cerrarModal.textContent = 'X';
    cerrarModal.classList.add('btn-cerrar'); // Clase desde _galeria.scss
    cerrarModal.onclick = function(){
        const body = document.querySelector('body');
        body.classList.remove('fijar-body'); // Clase desde _globales.scss
        overlay.remove();
    }
    overlay.appendChild(cerrarModal);

    // AÃ±adirlo al HTML
    const body = document.querySelector('body');
    body.appendChild(overlay); // Insertamos la const overlay en el Body.
    body.classList.add('fijar-body'); // Clase desde _globales.scss


}